.NoticeBoard {
    width: 50%;
    margin: 0 auto;
    /* padding: 5px; */
}

.Notice{
    background-color: #ecf0f1;
    margin: 16px auto;
    border: 1px solid black;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    padding: 16px;
    cursor: pointer;
    min-height: 100px;
    max-height: 100px;
    height: 100px;
}

.Notice:hover {
    border: 1px solid white;
    background:#3f51b5 ;
    color: white;
    cursor: pointer;
    -webkit-transition: .9s;
    -o-transition: .9s;
    transition: .9s;
}

.Button {
    border: 1px solid orangered;
    border-radius: 15px;
    color: red;
    background: white;
    padding: 3px 8px;
    font-size: 14px;
    text-decoration: none;
    outline: none;
    cursor: pointer;
}

.Button:hover {
    background: red;
    color: white;
}

.PageTitle {
    font-family: 'Roboto', sans-serif;
    font-weight: 400 !important;
}


