* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.loaderBg {
	width: 100vw;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background-color: rgba(255, 255, 255, 0.8);
	z-index: 2;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}


.fontNormal {
	font-weight: 300;
}
.sectionTitle {
	font-size: 20px;
	color: white;
	width: 100%;
	text-align: center;
}
.spacer {
	height: 30px;
	width: 100%;
}
.classSchedule {
	border-radius: 5px;
	overflow: hidden;
}
.classSchedule * {
	vertical-align: middle;
	font-size: 15px;
}
.dayCon div {
	cursor: pointer;
	background-color: #888;
	color: white;
	display: inline-block;
	width: calc(100% / 6);
	text-align: center;
	padding: 10px 0px;
}

.dayhead {
	text-align: center;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
	width: 100%;
	vertical-align: middle !important;
}

.today {
	pointer-events: none;
	background-color: #ff9933;
}

.dayhead:active,
.dayhead:hover {
	background-color: #ff7722;
}

.scheduleChild {
	border-bottom: thin solid white;
	background-color: rgba(0, 0, 0, 0.05);
}
.scheduleTime {
	height: 100%;
	text-align: center;
	padding: 10px;
	display: inline-block;
	width: 100px;
	background-color: rgba(0, 0, 0, 0.04);
}
.scheduleTime p, .scheduleSub p {
	line-height: 1.3;
	padding: 0;
	margin: 0;
}
.scheduleSub {
	height: 100%;
	padding: 10px 13px;
	display: inline-block;
	width: calc(80% - 100px);
	padding: 10px 15px;
}
.fontSecondary {
	color: gray;
}

.Row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 80%;
	margin: 0 auto;
}
  
.Column {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-preferred-size: 80%;
        flex-basis: 80%;
	-ms-flex: 1 1;
	    flex: 1 1;
}

.PageTitle {
	font-family: 'Roboto', sans-serif;
	font-weight: 400;
}
  



